namespace $ { export class $mol_app_bench_list_mol extends $mol_scroller {

	/// sample \
	sample() {
		return ""
	}

	/// header $mol_viewer childs / < sample
	@ $mol_mem()
	header( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.sample() )
		} )
	}

	/// rowers /
	rowers() {
		return [] as any[]
	}

	/// lister $mol_lister rows / 
	/// 	< header 
	/// 	< rowers
	@ $mol_mem()
	lister( next? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.rows = () => [].concat( this.header() , this.rowers() )
		} )
	}

	/// childs / < lister
	childs() {
		return [].concat( this.lister() )
	}

	/// rowerSelected# false
	@ $mol_mem_key()
	rowerSelected( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// rowerTitle# \
	rowerTitle( key : any ) {
		return ""
	}

	/// rowerContent# \
	rowerContent( key : any ) {
		return ""
	}

	/// rower# $mol_app_bench_list_mol_rower 
	/// 	selected > rowerSelected# 
	/// 	title < rowerTitle# 
	/// 	content < rowerContent#
	@ $mol_mem_key()
	rower( key : any , next? : any ) {
		return new $mol_app_bench_list_mol_rower().setup( obj => { 
			obj.selected = ( next? : any ) => this.rowerSelected( key , next )
			obj.title = () => this.rowerTitle( key )
			obj.content = () => this.rowerContent( key )
		} )
	}

} }

namespace $ { export class $mol_app_bench_list_mol_rower extends $mol_viewer {

	/// selected false
	@ $mol_mem()
	selected( next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// heightMinimal 56
	heightMinimal() {
		return 56
	}

	/// attr * mol_app_bench_list_mol_rower_selected < selected
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_app_bench_list_mol_rower_selected" : ()=> <any> this.selected() ,
		} )
	}

	/// eventToggle null
	@ $mol_mem()
	eventToggle( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// event * click > eventToggle
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( next? : any )=> <any> this.eventToggle( next ) ,
		} )
	}

	/// title \
	title() {
		return ""
	}

	/// titler $mol_viewer childs / < title
	@ $mol_mem()
	titler( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.title() )
		} )
	}

	/// content \
	content() {
		return ""
	}

	/// contenter $mol_viewer childs / < content
	@ $mol_mem()
	contenter( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.content() )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< contenter
	childs() {
		return [].concat( this.titler() , this.contenter() )
	}

} }

