namespace $ { export class $mol_stacker extends $mol_viewer {

	/// side false
	side() {
		return false
	}

	/// attr * mol_stacker_side < side
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_stacker_side" : ()=> <any> this.side() ,
		} )
	}

	/// main /
	main() {
		return [] as any[]
	}

	/// mainer $mol_viewer childs < main
	@ $mol_mem()
	mainer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => this.main()
		} )
	}

	/// addon /
	addon() {
		return [] as any[]
	}

	/// addoner $mol_viewer childs < addon
	@ $mol_mem()
	addoner( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => this.addon()
		} )
	}

	/// childs / 
	/// 	< mainer 
	/// 	< addoner
	childs() {
		return [].concat( this.mainer() , this.addoner() )
	}

} }

