namespace $ { export class $mol_portioner_indicator extends $mol_viewer {

	/// widthStyle \0
	widthStyle() {
		return "0"
	}

	/// field * style.width < widthStyle
	field() {
		return $mol_merge_dict( super.field() , {
			"style.width" : ()=> <any> this.widthStyle() ,
		} )
	}

} }

namespace $ { export class $mol_portioner extends $mol_viewer {

	/// portion 0
	portion() {
		return 0
	}

	/// indWidthStyle \0
	indWidthStyle() {
		return "0"
	}

	/// indicator $mol_portioner_indicator widthStyle < indWidthStyle
	@ $mol_mem()
	indicator( next? : any ) {
		return new $mol_portioner_indicator().setup( obj => { 
			obj.widthStyle = () => this.indWidthStyle()
		} )
	}

	/// childs / < indicator
	childs() {
		return [].concat( this.indicator() )
	}

} }

