namespace $ { export class $mol_bencher extends $mol_grider {

	/// results null
	results() {
		return <any> null
	}

	/// resultsSorted null
	resultsSorted() {
		return <any> null
	}

	/// records < resultsSorted
	records() {
		return this.resultsSorted()
	}

	/// colSort \
	colSort() {
		return ""
	}

	/// eventSortToggle# null
	@ $mol_mem_key()
	eventSortToggle( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// columnHeaderLabel# /
	columnHeaderLabel( key : any ) {
		return [] as any[]
	}

	/// columnHeaderSorter# $mol_icon_sort_asc
	@ $mol_mem_key()
	columnHeaderSorter( key : any , next? : any ) {
		return new $mol_icon_sort_asc()
	}

	/// columnHeaderContent# / 
	/// 	< columnHeaderLabel# 
	/// 	< columnHeaderSorter#
	columnHeaderContent( key : any ) {
		return [].concat( this.columnHeaderLabel( key ) , this.columnHeaderSorter( key ) )
	}

	/// columnHeader# $mol_bencher_header 
	/// 	eventClick > eventSortToggle# 
	/// 	childs < columnHeaderContent#
	@ $mol_mem_key()
	columnHeader( key : any , next? : any ) {
		return new $mol_bencher_header().setup( obj => { 
			obj.eventClick = ( next? : any ) => this.eventSortToggle( key , next )
			obj.childs = () => this.columnHeaderContent( key )
		} )
	}

	/// resultValue# \
	resultValue( key : any ) {
		return ""
	}

	/// resultPortion# 0
	resultPortion( key : any ) {
		return 0
	}

	/// resultPortioner# $mol_portioner portion < resultPortion#
	@ $mol_mem_key()
	resultPortioner( key : any , next? : any ) {
		return new $mol_portioner().setup( obj => { 
			obj.portion = () => this.resultPortion( key )
		} )
	}

	/// cellerContentNumber# / 
	/// 	< resultValue# 
	/// 	< resultPortioner#
	cellerContentNumber( key : any ) {
		return [].concat( this.resultValue( key ) , this.resultPortioner( key ) )
	}

} }

namespace $ { export class $mol_bencher_header extends $mol_floater {

	/// eventClick null
	@ $mol_mem()
	eventClick( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// event * click > eventClick
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( next? : any )=> <any> this.eventClick( next ) ,
		} )
	}

	/// hint @ \Click to sort by this column
	hint() {
		return this.localizedText( "hint" )
	}

	/// attr * title < hint
	attr() {
		return $mol_merge_dict( super.attr() , {
			"title" : ()=> <any> this.hint() ,
		} )
	}

} }

