namespace $ { export class $mol_app_bench extends $mol_stacker {

	/// tester $mol_app_bench_tester
	@ $mol_mem()
	tester( next? : any ) {
		return new $mol_app_bench_tester()
	}

	/// results null
	results() {
		return <any> null
	}

	/// columnHeaderLabel# /
	columnHeaderLabel( key : any ) {
		return [] as any[]
	}

	/// resultsColSort \
	@ $mol_mem()
	resultsColSort( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// resulter $mol_bencher 
	/// 	results < results 
	/// 	columnHeaderLabel# < columnHeaderLabel# 
	/// 	colSort > resultsColSort
	@ $mol_mem()
	resulter( next? : any ) {
		return new $mol_bencher().setup( obj => { 
			obj.results = () => this.results()
			obj.columnHeaderLabel = ( key : any ) => this.columnHeaderLabel( key )
			obj.colSort = ( next? : any ) => this.resultsColSort( next )
		} )
	}

	/// description \
	description() {
		return ""
	}

	/// descriptioner $mol_texter text < description
	@ $mol_mem()
	descriptioner( next? : any ) {
		return new $mol_texter().setup( obj => { 
			obj.text = () => this.description()
		} )
	}

	/// mainPage $mol_app_bench_mainer 
	/// 	title < title 
	/// 	tester < tester 
	/// 	body / 
	/// 		< resulter 
	/// 		< descriptioner
	@ $mol_mem()
	mainPage( next? : any ) {
		return new $mol_app_bench_mainer().setup( obj => { 
			obj.title = () => this.title()
			obj.tester = () => this.tester()
			obj.body = () => [].concat( this.resulter() , this.descriptioner() )
		} )
	}

	/// main / < mainPage
	main() {
		return [].concat( this.mainPage() )
	}

	/// addonerTitle @ \Samples
	addonerTitle() {
		return this.localizedText( "addonerTitle" )
	}

	/// menuOptions /
	menuOptions() {
		return [] as any[]
	}

	/// menu $mol_lister rows < menuOptions
	@ $mol_mem()
	menu( next? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.rows = () => this.menuOptions()
		} )
	}

	/// addonPage $mol_pager 
	/// 	title < addonerTitle 
	/// 	body / < menu
	@ $mol_mem()
	addonPage( next? : any ) {
		return new $mol_pager().setup( obj => { 
			obj.title = () => this.addonerTitle()
			obj.body = () => [].concat( this.menu() )
		} )
	}

	/// addon / < addonPage
	addon() {
		return [].concat( this.addonPage() )
	}

	/// menuOptionerChecked# false
	@ $mol_mem_key()
	menuOptionerChecked( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// menuOptionerTitle# \
	menuOptionerTitle( key : any ) {
		return ""
	}

	/// menuOptioner# $mol_checker_ticker 
	/// 	heightMinimal 36 
	/// 	checked > menuOptionerChecked# 
	/// 	label / < menuOptionerTitle#
	@ $mol_mem_key()
	menuOptioner( key : any , next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.heightMinimal = () => 36
			obj.checked = ( next? : any ) => this.menuOptionerChecked( key , next )
			obj.label = () => [].concat( this.menuOptionerTitle( key ) )
		} )
	}

	/// columnHeaderLabelSample @ \Sample
	columnHeaderLabelSample() {
		return this.localizedText( "columnHeaderLabelSample" )
	}

} }

namespace $ { export class $mol_app_bench_mainer extends $mol_pager {

	/// tester null
	tester() {
		return <any> null
	}

	/// childs / 
	/// 	< header 
	/// 	< bodier 
	/// 	< tester
	childs() {
		return [].concat( this.header() , this.bodier() , this.tester() )
	}

} }

namespace $ { export class $mol_app_bench_tester extends $mol_viewer {

	/// tagName \iframe
	tagName() {
		return "iframe"
	}

} }

